import { useEffect, useState, useCallback, useMemo } from 'react'
import { useWeb3Context } from 'web3-react'

import {
  isAddress,
  getCascadeContract,
  getDepositInfo,
  getSBSTokenContract,
  getLPTokenContract,
  getEtherBalance,
  // getSBSTokenBalance,
  // getLPTokenBalance,
  getTokenBalance,
  getLPTokenAllowance,
  rewardsPool,
  // getTotalSupply,
  // TOKEN_ADDRESSES,
  // upgradeMultiplierLevel,
  getUserOwedTo,
} from '../utils'
import { utils } from 'ethers'

export function useBlockEffect(functionToRun) {
  const { library } = useWeb3Context()

  useEffect(() => {
    if (library) {
      function wrappedEffect(blockNumber) {
        functionToRun(blockNumber)
      }
      library.on('block', wrappedEffect)
      return () => {
        library.removeListener('block', wrappedEffect)
      }
    }
  }, [library, functionToRun])
}



export function useActiveNetwork() {
  const { library } = useWeb3Context()

  const [activeNetwork, setActiveNetwork] = useState(false)

  const updateNetwork = useCallback(() => {
    library.getNetwork()
      .then(value => {
        setActiveNetwork(value.chainId + '' === process.env.REACT_APP_CHAIN_ID + '')
      })
      .catch((err) => {
        setActiveNetwork(null)
      })
    return () => {
      setActiveNetwork()
    }
  }, [library])

  useEffect(() => {
    return updateNetwork()
  }, [updateNetwork])

  useBlockEffect(updateNetwork)

  return activeNetwork
}

export function useCascadeContract(tokenAddress, withSignerIfPossible = true) {
  const { library, account } = useWeb3Context()

  return useMemo(() => {
    try {
      return getCascadeContract(library, withSignerIfPossible ? account : undefined)
    } catch {
      return null
    }
  }, [library, withSignerIfPossible])
}

