export function useAddressBalance(address, tokenAddress) {
  const { library } = useWeb3Context()

  const [balance, setBalance] = useState()

  const updateBalance = useCallback(() => {
    if (isAddress(address) && (tokenAddress === 'ETH' || isAddress(tokenAddress))) {
      let stale = false
        ; (tokenAddress === 'ETH' ? getEtherBalance(address, library) : getTokenBalance(tokenAddress, address, library))
          .then(value => {
            if (!stale) {
              setBalance(value)
            }
          })
          .catch((err) => {
            if (!stale) {
              setBalance(null)
            }
          })
      return () => {
        stale = true
        setBalance()
      }
    }
  }, [address, library, tokenAddress])

  useEffect(() => {
    return updateBalance()
  }, [updateBalance])

  useBlockEffect(updateBalance)

  return balance
}

export function useLPAddressAllowance(address, spenderAddress) {
  const { library } = useWeb3Context()

  const [allowance, setAllowance] = useState()

  const updateAllowance = useCallback(() => {
    if (isAddress(address) && isAddress(spenderAddress)) {
      let stale = false

      getLPTokenAllowance(address, spenderAddress, library)
        .then(allowance => {
          if (!stale) {
            setAllowance(allowance)
          }
        })
        .catch(() => {
          if (!stale) {
            setAllowance(null)
          }
        })

      return () => {
        stale = true
        setAllowance()
      }
    }
  }, [address, library, spenderAddress])

  useEffect(() => {
    return updateAllowance()
  }, [updateAllowance])

  useBlockEffect(updateAllowance)

  return allowance
}

export function useRewardsPool() {
  const { library } = useWeb3Context()

  const [balance, setBalance] = useState()

  const updateRewardsPool = useCallback(() => {
    let stale = false

    rewardsPool(library)
      .then(value => {
        if (!stale) {
          setBalance(value)
        }
      })
      .catch((err) => {
        if (!stale) {
          setBalance(null)
        }
      })
    return () => {
      stale = true
      setBalance()
    }

  }, [library])

  useEffect(() => {
    return updateRewardsPool()
  }, [updateRewardsPool])

  useBlockEffect(updateRewardsPool)

  return balance
}

export function useTotalSupply(contract) {
  const [totalSupply, setTotalSupply] = useState()

  const updateTotalSupply = useCallback(() => {
    if (!!contract) {
      let stale = false
      contract
        .totalSupply()
        .then(value => {
          if (!stale) {
            setTotalSupply(value)
          }
        })
        .catch((err) => {
          if (!stale) {
            setTotalSupply(null)
          }
        })
      return () => {
        stale = true
        setTotalSupply()
      }
    }
  }, [contract])

  useEffect(() => {
    return updateTotalSupply()
  }, [updateTotalSupply])

  useBlockEffect(updateTotalSupply)

  return totalSupply && Math.round(Number(utils.formatEther(totalSupply)))
}

export function useDepositInfo(address) {
  const [depositInfo, setDepositInfo] = useState()
  const { library } = useWeb3Context()

  const updateDepositInfo = useCallback(() => {
    if (!!address) {
      let stale = false
      getDepositInfo(address, library)
        .then(value => {
          if (!stale) setDepositInfo(value)
        })
        .catch(() => {
          if (!stale) setDepositInfo(null)
        })

      return () => {
        stale = true
        setDepositInfo()
      }
    }
  }, [address, library])

  useEffect(() => {
    return updateDepositInfo()
  }, [updateDepositInfo])

  useBlockEffect(updateDepositInfo)

  return depositInfo && depositInfo
}

