import { ethers } from 'ethers'

import SBSTOKEN from './StaysBASEToken.json'
import ERC20_ABI from './erc20.json'
import LPTOKEN from './ERC20UpgradeSafe.json'
import CASCADE from './Cascade.json'

import UncheckedJsonRpcSigner from './signer'

export const TOKEN_ADDRESSES = {
  ETH: 'ETH',
  SBS: 'SBS',
  LP: process.env.REACT_APP_LP_NAME,
  LPTOKEN: process.env.REACT_APP_LPTOKEN_ADDR,
  SBSTOKEN: process.env.REACT_APP_SBSTOKEN_ADDR,
  CASCADE: process.env.REACT_APP_CASCADE_ADDR,
  ETH_DEC: 18,
  LPTOKEN_DEC: 18,
  SBSTOKEN_DEC: 9,
  TIME_DEC: 3,
  DISP_DEC: 3
}

export const TOKEN_SYMBOLS = Object.keys(TOKEN_ADDRESSES).reduce((o, k) => {
  o[k] = k
  return o
}, {})

export const ERROR_CODES = [
  'INVALID_AMOUNT',
  'INVALID_TRADE',
  'INSUFFICIENT_ETH_GAS',
  'INSUFFICIENT_SELECTED_TOKEN_BALANCE',
  'INSUFFICIENT_ALLOWANCE'
].reduce((o, k, i) => {
  o[k] = i
  return o
}, {})

export const TRADE_TYPES = ['DEPOSIT', 'WITHDRAW', 'UNLOCK'].reduce((o, k, i) => {
  o[k] = i
  return o
}, {})

export function isAddress(value) {
  try {
    ethers.utils.getAddress(value)
    return true
  } catch {
    return false
  }
}

// account is optional
export function getProviderOrSigner(library, account) {
  return account ? new UncheckedJsonRpcSigner(library.getSigner(account)) : library
}

// account is optional
export function getContract(address, ABI, library, account) {
  if (!isAddress(address) || address === ethers.constants.AddressZero) {
    throw Error(`Invalid 'address' parameter '${address}'.`)
  }

  return new ethers.Contract(address, ABI, getProviderOrSigner(library, account))
}

export function getTokenContract(tokenAddress, library, account) {
  return getContract(tokenAddress, LPTOKEN.abi, library, account)
}

// SBS Token Contract
export function getSBSTokenContract(library, account) {
  return getContract(TOKEN_ADDRESSES.SBSTOKEN, SBSTOKEN.abi, library, account)
}

// LP Token Contract
export function getLPTokenContract(library, account) {
  return getContract(TOKEN_ADDRESSES.LPTOKEN, ERC20_ABI, library, account)
}

// Cascade Contract
export function getCascadeContract(library, account) {
  return getContract(TOKEN_ADDRESSES.CASCADE, CASCADE.abi, library, account)
}

// depositLPToken
export async function depositLPToken(amount, library) {
  return getCascadeContract(library).deposit(amount);
}

// withdrawLPTokens
export async function withdrawLPTokens(library) {
  return getCascadeContract(library).withdrawLPTokens();
}

// getDepositInfo
export async function getDepositInfo(address, library) {
  if (!isAddress(address)) {
    throw Error(`Invalid 'address' parameter '${address}'`)
  }
  return getCascadeContract(library).depositInfo(address);
}

// userDepositSeconds
export async function userDepositSeconds(address, library) {
  if (!isAddress(address)) {
    throw Error(`Invalid 'address' parameter '${address}'`)
  }
  return getCascadeContract(library).userDepositSeconds(address);
}

// totalDepositSeconds
export async function totalDepositSeconds(library) {
  return getCascadeContract(library).totalDepositSeconds();
}

// totalDepositSeconds
export async function rewardsPool(library) {
  return getCascadeContract(library).rewardsPool();
}

// owedTo
export async function getUserOwedTo(address, library) {
  if (!isAddress(address)) {
    throw Error(`Invalid 'address' parameter '${address}'`)
  }
  return getCascadeContract(library).owedTo(address);
}


// owedTo
export async function getTotalSupply(library) {
  return getSBSTokenContract(library).totalSupply();
}

// get the ether balance of an address
export async function getEtherBalance(address, library) {
  if (!isAddress(address)) {
    throw Error(`Invalid 'address' parameter '${address}'`)
  }

  return library.getBalance(address)
}

// get the token balance of an address
export async function getSBSTokenBalance(tokenAddress, address, library) {
  if (!isAddress(tokenAddress) || !isAddress(address)) {
    throw Error(`Invalid 'tokenAddress' or 'address' parameter '${tokenAddress}' or '${address}'.`)
  }

  return getSBSTokenContract(library).balanceOf(address)
}

export async function getSBSTokenAllowance(address, spenderAddress, library) {
  if (!isAddress(address) || !isAddress(spenderAddress)) {
    throw Error(
      "Invalid 'address' or 'tokenAddress' or 'spenderAddress' parameter" +
      `'${address}' or '${spenderAddress}'.`
    )
  }

  return getSBSTokenContract(library).allowance(address, spenderAddress)
}

// get the token balance of an address
export async function getLPTokenBalance(tokenAddress, address, library) {
  if (!isAddress(tokenAddress) || !isAddress(address)) {
    throw Error(`Invalid 'tokenAddress' or 'address' parameter '${tokenAddress}' or '${address}'.`)
  }
  return getLPTokenContract(library).balanceOf(address)
}

export async function getLPTokenAllowance(address, spenderAddress, library) {
  if (!isAddress(address) || !isAddress(spenderAddress)) {
    throw Error(
      "Invalid 'address' or 'spenderAddress' parameter" +
      `'${address}' or '${spenderAddress}'.`
    )
  }

  return getLPTokenContract(library).allowance(address, spenderAddress)
}

export function depositInfoFormatter(depositInfo) {
  if (!depositInfo) return {
    _multiplierLevel: '0.0',
    _depositTimestamp: 0,
    _lpTokensDeposited: 0,
    _mostRecentBASEWithdrawal: 0,
    _totalDepositSeconds: 0,
    _userDepositSeconds: 0,
  };
  return {
    _multiplierLevel: depositInfo._multiplierLevel.toFixed(1),
    _depositTimestamp: parseFloat(amountFormatter(depositInfo._depositTimestamp, 0, 0)),
    _lpTokensDeposited: parseFloat(amountFormatter(depositInfo._lpTokensDeposited, TOKEN_ADDRESSES.LPTOKEN_DEC, 8)),
    _mostRecentBASEWithdrawal: parseFloat(amountFormatter(depositInfo._mostRecentBASEWithdrawal, 0, 0)),
    _totalDepositSeconds: parseFloat(amountFormatter(depositInfo._totalDepositSeconds, TOKEN_ADDRESSES.TIME_DEC, 0)),
    _userDepositSeconds: parseFloat(amountFormatter(depositInfo._userDepositSeconds, TOKEN_ADDRESSES.TIME_DEC, 0)),
  }
}





