import React from 'react'
import styled from 'styled-components'

const SNavItemDropdown = styled.div`
  display: ${({ isNavItemOpen }) => isNavItemOpen ? 'flex' : 'none'};
  flex-direction: column;
  background: white;
  position: absolute;
  top: 74px;
  left: 158px;
  box-shadow: 2px 2px 6px rgba(0,0,0,.3);
`

const SNavItem = styled.a`
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: rgb(32, 32, 32);
  padding: 10px 20px;
  line-height: 20px;
  transition: none;
  text-decoration: none;
  &:hover {
    background-image: linear-gradient(90deg,#f3ba2f,#8f7023 25%,#b88e28 52%,#f3ba2f 75%,#8b6c23);
    color: white;
  }
`

function NavItemDropdown({ isNavItemOpen, setIsNavItemOpen }) {

  return (
    <SNavItemDropdown isNavItemOpen={isNavItemOpen}>
      {/* <SNavItem href={`${process.env.REACT_APP_BASIC_URL}`}>Basics</SNavItem>
      <SNavItem onClick={setIsNavItemOpen} href={`${process.env.REACT_APP_WHITEPAPER_URL}`} target="_blank">Whitepaper</SNavItem> */}
      <SNavItem onClick={setIsNavItemOpen} href={`${process.env.REACT_APP_LITEPAPER_URL}`} target="_blank">Lightpaper</SNavItem>
    </SNavItemDropdown>
  )
}

export default NavItemDropdown
