import React, { useState } from 'react'

import styled from 'styled-components'

const StatusItemDiv = styled.div`
  position: relative;
  display: inline-block;
  width: 32%;
  margin-right: 1%;
  margin-bottom: 1%;
  height: auto;
  border: 1px solid rgba(0,0,0,.2);
  border-radius: 5px;
  padding: 10px;
  line-height: 1.5;
  box-sizing: border-box;
  overflow: unset;
  font-size: 14px;
  &:last-child{
    margin-right: 0;
  }
  @media (max-width: 600px) {
    width: 49%;
    &:nth-child(2){
      margin-right: 0;
    }
    &:last-child{
      width: 99%;
    }    
  }
`

const StatusItemTitle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const StatusItemDescriptionBtn = styled.div`
  cursor: pointer;
  display: block;
  font-size: 18px;
  line-height: 1;
`

const StatusItemDescription = styled.div`
  position: absolute;
  right: 0;
  top: 50%;
  background-color: black;
  border-radius: 5px;
  color: white;
  padding: 10px;
  width: 400px;
  max-width: 600px;
  z-index: 100;
  text-align: left;
  box-shadow: 0px 0px 8px rgba(255,255,255,.5);
`

const StatusItemValue = styled.div`
  text-align: left;
  width: 100%;
  font-weight: bold;
`

export default function StatusItem({ title = "APY", value = "69%", description = "descriperer", styleLeft = "auto" }) {
  const [showDesc, setShowDesc] = useState(false)
  return (
    <StatusItemDiv>
      <StatusItemTitle>
        <div>{title}</div>
        <StatusItemDescriptionBtn
          onClick={() => setShowDesc(true)}
          onMouseOut={() => setShowDesc(false)}>
          <svg viewBox="64 64 896 896" focusable="false" data-icon="info-circle" width="1em" height="1em" fill="currentColor" aria-hidden="true">
            <path d="M512 64C264.6 64 64 264.6 64 512s200.6 448 448 448 448-200.6 448-448S759.4 64 512 64zm0 820c-205.4 0-372-166.6-372-372s166.6-372 
                372-372 372 166.6 372 372-166.6 372-372 372z"></path>
            <path d="M464 336a48 48 0 1096 0 48 48 0 10-96 0zm72 112h-48c-4.4 0-8 3.6-8 8v272c0 4.4 3.6 8 8 8h48c4.4 0 8-3.6 8-8V456c0-4.4-3.6-8-8-8z"></path>
          </svg>
        </StatusItemDescriptionBtn>
      </StatusItemTitle>
      <StatusItemValue>{value}</StatusItemValue>
      <StatusItemDescription style={{ display: (showDesc ? 'block' : 'none'), left: styleLeft, right: (styleLeft === 'auto' ? 0 : 'auto') }}>
        {description}
      </StatusItemDescription>
    </StatusItemDiv >
  )
}
