import React, { useState } from 'react'

import styled from 'styled-components'
import { amountFormatter, TOKEN_ADDRESSES, TRADE_TYPES } from '../utils'
import Button from './Button'

const StatusItemDiv = styled.div`
  position: relative;
  display: inline-block;
  width: 100%;
  height: auto;
  padding: 10px 0;
  line-height: 1.5;
  box-sizing: border-box;
  overflow: unset;
  z-index: 10;
`

const BalanceDescription = styled.div`
  width: 100%;
  text-align: right;
  font-size: 12.5px;  
`

const BalanceInput = styled.input`
  border: 1px solid rgba(0,0,0,.2);
  border-radius: 5px;
  padding: 16px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 600;
  outline: none;
`

const RewardStatus = styled.div`
  margin-top: 20px;
  position: relative;
  border-radius: 8px;
  border-left: 8px solid #ff2d55;
  padding: 16px 0;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  & > div{
    padding: 0 20px;
  }
  img{
    width: 50px;
    height: 50px;
  }
`


const StatusItemTitle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const StatusItemDescriptionBtn = styled.div`
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  margin: 0 10px;
`

const StatusItemDescription = styled.div`
  position: absolute;
  right: 0;
  top: 50%;
  background-color: black;
  border-radius: 5px;
  color: white;
  padding: 10px;
  max-width: 300px;
  font-size: 14px;
  z-index: 10;
`

const StatusItemValue = styled.div`
  text-align: left;
  width: 100%;
  font-weight: bold;
`

const WarningArea = styled.div`
  margin-top: 20px;
  position: relative;
  border-radius: 0;
  border-left: 8px solid #7424cc;
  background-color: #912dff;
  padding: 16px 20px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: white;
`

export default function DepositContent({ account, balanceLP, deposit, allowanceLP, userCurrentReward, pending, currentTransactionType, setCurrentTransaction }) {
  const [showDesc, setShowDesc] = useState(false);
  const [depositAmount, setDepositAmount] = useState('');
  const [isValid, setIsValid] = useState(true);
  const [isRquested, setIsRequested] = useState(false);
  const amountLP = amountFormatter(balanceLP, TOKEN_ADDRESSES.LPTOKEN_DEC, 8);

  const inputProcess = (e) => {
    const inputVal = e.currentTarget.value;
    setDepositAmount(inputVal);
    if (parseFloat(inputVal) > 0 && parseFloat(inputVal) <= parseFloat(amountLP)) {
      setIsValid(true);
    } else if (inputVal === '' || parseFloat(inputVal) === 0) {
      setIsValid(true);
    } else {
      setIsValid(false);
    }
  };

  const depositProcess = (amount) => {
    setIsRequested(true);
    deposit(amount).then((response) => {
      setCurrentTransaction(
        response.hash,
        TRADE_TYPES.DEPOSIT,
        depositAmount
      )
      setDepositAmount('');
      setIsRequested(false);
    });
  }
  return (
    <StatusItemDiv>
      <BalanceDescription >
        <p>Wallet Balance: {`${amountLP}`}&nbsp;<span>({TOKEN_ADDRESSES.LP})</span></p>
      </BalanceDescription>
      <BalanceInput type="number" placeholder="Enter Amount"
        disabled={(isRquested || pending) ? 'disabled' : ''}
        min={0} value={depositAmount}
        onChange={inputProcess} style={{ borderColor: (isValid) ? 'rgba(0,0,0,.2)' : 'red' }}
      />
      <RewardStatus>
        <div><img src={`${process.env.PUBLIC_URL}/img/logo.png`} alt="SBSlogo" /></div>
        <div>
          <StatusItemTitle>
            <div>Your Estimated Rewards</div>
            <StatusItemDescriptionBtn
              onClick={() => setShowDesc(true)}
              onMouseOut={() => setShowDesc(false)}>
              <svg viewBox="64 64 896 896" focusable="false" data-icon="info-circle" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                <path d="M512 64C264.6 64 64 264.6 64 512s200.6 448 448 448 448-200.6 448-448S759.4 64 512 64zm0 820c-205.4 0-372-166.6-372-372s166.6-372 
                    372-372 372 166.6 372 372-166.6 372-372 372z"></path>
                <path d="M464 336a48 48 0 1096 0 48 48 0 10-96 0zm72 112h-48c-4.4 0-8 3.6-8 8v272c0 4.4 3.6 8 8 8h48c4.4 0 8-3.6 8-8V456c0-4.4-3.6-8-8-8z"></path>
              </svg>
            </StatusItemDescriptionBtn>
          </StatusItemTitle>
          <StatusItemValue>{`${amountFormatter(userCurrentReward, TOKEN_ADDRESSES.SBSTOKEN_DEC)}`} {TOKEN_ADDRESSES.SBS}</StatusItemValue>
          <StatusItemDescription style={{ display: (showDesc ? 'block' : 'none') }}>
            Estimated rewards assume you have achieved the maximum reward multiplier.
            </StatusItemDescription>
        </div>
      </RewardStatus>
      {!account && (
        <WarningArea>Connect your wallet to the Binance Smart Chain</WarningArea>
      )}
      <Button style={{ marginTop: '18px' }} disabled={(amountLP <= 0 || !depositAmount || !isValid || !allowanceLP || isRquested || pending) ? 'disabled' : ''}
        text={'Deposit'}
        onClick={() => { depositProcess(depositAmount) }}
        pending={pending}
        currentTransactionType={currentTransactionType}
        tradeType={TOKEN_ADDRESSES.DEPOSIT}
      />
    </StatusItemDiv >
  )
}
