export default function Main() {
  const { library, account } = useWeb3Context()
  const ready = true;

  // selected token
  // const [selectedTokenSymbol, setSelectedTokenSymbol] = useState(TOKEN_SYMBOLS.ETH);

  // const sbsToken = useSBSTokenContract(TOKEN_ADDRESSES.SBSTOKEN);
  const lpToken = useLPTokenContract(TOKEN_ADDRESSES.LPTOKEN);
  const cascadeContract = useCascadeContract(TOKEN_ADDRESSES.CASCADE);

  // get balances
  const balanceETH = useAddressBalance(account, TOKEN_ADDRESSES.ETH);
  // const balanceSBS = useAddressBalance(account, TOKEN_ADDRESSES.SBS);
  const balanceLP = useAddressBalance(account, TOKEN_ADDRESSES.LPTOKEN);
  const allowanceLP = useLPAddressAllowance(account, TOKEN_ADDRESSES.CASCADE);

  const rewardsPool = useRewardsPool();
  // const userMultiplierLevel = useUserMultiplierLevel(account);
  const depositInfo = useDepositInfo(account);
  const userCurrentReward = useOwedTo(account);
  const activeNetwork = useActiveNetwork();

  const totalSBSStr = useAddressBalance(TOKEN_ADDRESSES.CASCADE, TOKEN_ADDRESSES.SBSTOKEN);
  const totalSBS = amountFormatter(totalSBSStr, TOKEN_ADDRESSES.SBSTOKEN_DEC, 3);
  const totalLPStr = useAddressBalance(TOKEN_ADDRESSES.CASCADE, TOKEN_ADDRESSES.LPTOKEN);  
  const totalLP = amountFormatter(totalLPStr, TOKEN_ADDRESSES.LPTOKEN_DEC, 3);

  const tempAPY = (100 * (Math.pow((1 + (totalSBS / totalLP) / 60 * 0.000046), 365/90) -1)).toFixed(2);
  // console.log('---- tempAPY: ',tempAPY);

  const apyVal = parseFloat(tempAPY);

  // console.log('--------', rewardsPool, depositInfo, userCurrentReward, activeNetwork);

  async function unlock() {
    const contract = lpToken
    const spenderAddress = TOKEN_ADDRESSES.CASCADE;

    const estimatedGasLimit = await contract.estimate.approve(spenderAddress, ethers.constants.MaxUint256)
    const estimatedGasPrice = await library
      .getGasPrice()
      .then(gasPrice => gasPrice.mul(ethers.utils.bigNumberify(150)).div(ethers.utils.bigNumberify(100)))

    return contract.approve(spenderAddress, ethers.constants.MaxUint256, {
      gasLimit: calculateGasMargin(estimatedGasLimit, GAS_MARGIN),
      gasPrice: estimatedGasPrice
    })
  }

  // async function updateRewardPool(amount) {
  //   const parsedAmount = ethers.utils.parseUnits(amount, TOKEN_ADDRESSES.SBSTOKEN_DEC);

  //   // console.log('---- parsedAmount', parsedAmount);

  //   const estimatedGasPrice = await library
  //     .getGasPrice()
  //     .then(gasPrice => gasPrice.mul(ethers.utils.bigNumberify(150)).div(ethers.utils.bigNumberify(100)))

  //   const estimatedGasLimit = await cascadeContract.estimate.adminWithdrawBASE(
  //     TOKEN_ADDRESSES.CASCADE, parsedAmount);      
  //   return cascadeContract.adminWithdrawBASE(TOKEN_ADDRESSES.CASCADE, parsedAmount, {
  //     gasLimit: calculateGasMargin(estimatedGasLimit, GAS_MARGIN),
  //     gasPrice: estimatedGasPrice
  //   })
  // }

  async function deposit(amount) {
    if (!amount) {
      alert('Please Input Amount');
      return {};
    }
    const parsedAmount = ethers.utils.parseUnits(amount, TOKEN_ADDRESSES.LPTOKEN_DEC);

    const estimatedGasPrice = await library
      .getGasPrice()
      .then(gasPrice => gasPrice.mul(ethers.utils.bigNumberify(150)).div(ethers.utils.bigNumberify(100)))

    const estimatedGasLimit = await cascadeContract.estimate.deposit(parsedAmount);

    // await cascadeContract.upgradeMultiplierLevel();
    return cascadeContract.deposit(parsedAmount, {
      gasLimit: calculateGasMargin(estimatedGasLimit, GAS_MARGIN),
      gasPrice: estimatedGasPrice
    })
  }

  // validate deposit functionality
  const validateDeposit = useCallback(
    numberOfLP => {
      // validate passed amount
      let parsedValue
      try {
        parsedValue = ethers.utils.parseUnits(numberOfLP, TOKEN_ADDRESSES.LPTOKEN_DEC)
      } catch (error) {
        error.code = ERROR_CODES.INVALID_AMOUNT
        throw error
      }

      // the following are 'non-breaking' errors that will still return the data
      let errorAccumulator
      // validate minimum ether balance
      if (balanceETH.lt(ethers.utils.parseEther('.01'))) {
        const error = Error()
        error.code = ERROR_CODES.INSUFFICIENT_ETH_GAS
        if (!errorAccumulator) {
          errorAccumulator = error
        }
      }

      // validate minimum socks balance
      if (balanceLP.lt(parsedValue)) {
        const error = Error()
        error.code = ERROR_CODES.INSUFFICIENT_SELECTED_TOKEN_BALANCE
        if (!errorAccumulator) {
          errorAccumulator = error
        }
      }

      // validate allowance
      if (allowanceLP.lt(parsedValue)) {
        const error = Error()
        error.code = ERROR_CODES.INSUFFICIENT_ALLOWANCE
        if (!errorAccumulator) {
          errorAccumulator = error
        }
      }

      return {
        inputValue: parsedValue,
        error: errorAccumulator
      }
    },
    [
      allowanceLP,
      balanceETH,
      balanceLP,
    ]
  )
  // validate deposit functionality
  const validateWithdraw = useCallback(
    numberOfLP => {
      // validate passed amount
      let parsedValue
      try {
        parsedValue = ethers.utils.parseUnits(numberOfLP, TOKEN_ADDRESSES.LPTOKEN_DEC)
      } catch (error) {
        error.code = ERROR_CODES.INVALID_AMOUNT
        throw error
      }

      // the following are 'non-breaking' errors that will still return the data
      let errorAccumulator
      // validate minimum ether balance
      if (balanceETH.lt(ethers.utils.parseEther('.01'))) {
        const error = Error()
        error.code = ERROR_CODES.INSUFFICIENT_ETH_GAS
        if (!errorAccumulator) {
          errorAccumulator = error
        }
      }

      return {
        inputValue: parsedValue,
        error: errorAccumulator
      }
    },
    [
      balanceETH,
    ]
  )

  async function withdraw() {
    const estimatedGasPrice = await library
      .getGasPrice()
      .then(gasPrice => gasPrice.mul(ethers.utils.bigNumberify(150)).div(ethers.utils.bigNumberify(100)))

    const estimatedGasLimit = await cascadeContract.estimate.withdrawLPTokens();

    // await cascadeContract.upgradeMultiplierLevel();
    return cascadeContract.withdrawLPTokens({
      gasLimit: calculateGasMargin(estimatedGasLimit, GAS_MARGIN),
      gasPrice: estimatedGasPrice
    })
  }

  // const totalSupply = useTotalSupply(sbsToken);
  // console.log('---- totalSupply: ', totalSupply);
  // console.log('---- cascade contract', cascadeContract);
  // const setSBSToken = useSetBaseToken(TOKEN_ADDRESSES.SBSTOKEN, cascadeContract);

  // console.log('---- balanceLP', balanceLP);

  return (
    <div>
      <Navbar />
      <Body
        ready={ready}
        overlay={true}
        balanceETH={balanceETH}
        balanceLP={balanceLP}
        allowanceLP={allowanceLP}
        rewardsPool={rewardsPool}
        apyVal={apyVal}
        userCurrentReward={userCurrentReward}
        deposit={deposit}
        withdraw={withdraw}
        validateDeposit={validateDeposit}
        validateWithdraw={validateWithdraw}
        depositInfo={depositInfo}
        unlock={unlock}
        activeNetwork={activeNetwork}
      />
    </div>
  )
}
