import React from 'react'
import styled from 'styled-components'

// import baseLogo from "./../General/Navbar/baseLogo.png";
// import baseLogo from './baseLogo.png'

// import BaseBtn from './../BaseBtn'
// import SocialMediaBundle from './SocialMediaBundle'

const SMobileContentContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  width: 100%;
  transition: ease-in-out height .25s;
  height: ${(props) => props.isMobileNavOpen ? 'auto' : '0px'};
  margin-top: 3px;
  display: none;
  border-bottom: 1px solid rgba(0,0,0, .12);
  padding-bottom: ${(props) => props.isMobileNavOpen ? '24px' : '0px'};
  
  @media (max-width: 900px) {
    display: flex;
  }
`

const SNavLink = styled.a`
  margin-left: 24px;
  padding: 24px;
  text-transform: uppercase;
  color: #202020;
  font-weight: 700;
  font-size: 12px;
  text-align: center;
`

// const SBaseLogoA = styled.a`
//   display: flex;
//   align-items: center;
//   justify-content: center;
//   margin-top: 20px;
//   margin-left: 12px;
//   img {
//     height: 48px;
//   }
// `

const SMobileContentWrapper = styled.div`
  display: ${(props) => props.isMobileNavOpen ? 'flex' : 'none'};
  flex-direction: column;
  align-items: center;
  justify-content: center;
`

function MobileNavContent(props) {
  return (
    <SMobileContentContainer isMobileNavOpen={props.isMobileNavOpen}>
      <SMobileContentWrapper isMobileNavOpen={props.isMobileNavOpen}>
        {/* <SBaseLogoA href={process.env.PUBLIC_URL}>
          <img alt="Base Logo" src={baseLogo} />
        </SBaseLogoA> */}
        <SNavLink href={`${process.env.REACT_APP_WHITEPAPER_URL}`}>Learn</SNavLink>
        <SNavLink href={`${process.env.REACT_APP_JOIN_URL}`}>Join</SNavLink>
        <SNavLink href={`${process.env.REACT_APP_GITHUB_URL}`}>Github</SNavLink>
        <SNavLink href={process.env.REACT_APP_CASCADE_URL}>Cascade</SNavLink>
        <SNavLink href={process.env.REACT_APP_DASHBOARD_URL}>Dashboard</SNavLink>
        {/* <BaseBtn href={process.env.PUBLIC_URL} marginRight={'-20px'} /> */}
      </SMobileContentWrapper>
    </SMobileContentContainer>
  )
}

export default MobileNavContent
