import React, { useState, useCallback, useEffect } from 'react'
import styled from 'styled-components'
import { Spinner } from '../../theme'
import Circle from '../../components/Gallery/circle.svg'
import { useWeb3Context } from 'web3-react'
// import { Link } from 'react-router-dom'

import { useAppContext } from '../../context'
import {
  amountFormatter,
  depositInfoFormatter,
  TOKEN_ADDRESSES,
  TRADE_TYPES
} from '../../utils'
import StatusItem from '../../components/StatusItem'
import DepositContent from '../../components/DepositContent'
import WithdrawContent from '../../components/WithdrawContent'
import StatsContent from '../../components/StatsContent'
import Checkout from '../../components/Checkout'

export function Header({ ready, setShowConnect, allowanceLP, unlock, currentTransactionType, setCurrentTransaction, balanceETH, activeNetwork, pending }) {
  const { account } = useWeb3Context()

  function handleAccount() {
    if (account && !allowanceLP && activeNetwork) {
      unlock().then(({ hash }) => {
        setCurrentTransaction(hash, TRADE_TYPES.UNLOCK, undefined);
      });
      return;
    }
    setShowConnect(true)
  }

  return (
    <HeaderFrame>
      {/* <AccountElement active={!!account} style={{ pointerEvents: 'auto' }}>
        {account && balanceETH ? (
          <BalanceText style={{ flexShrink: 0 }} pl="0.75rem" pr="0.5rem" fontWeight={500}>
            {balanceETH?.toSignificant(4)} ETH
          </BalanceText>
        ) : null}
        <Web3Status />
      </AccountElement> */}

      <Account onClick={() => handleAccount()}>
        {(account && allowanceLP) ? (
          <SockCount>{account.slice(0, 12)}...</SockCount>
        ) : (
            !account ? (
              <SockCount>Connect Wallet</SockCount>
            ) : (
                <>
                  <SockCount>Unlock Wallet
                </SockCount>
                  {(currentTransactionType === TRADE_TYPES.UNLOCK && pending) && (
                    <SpinnerWrapper src={Circle} alt="loader" />
                  )}
                </>
              )
          )}
        <Status ready={ready} account={account} />
      </Account>
    </HeaderFrame>
  )
}

const HeaderFrame = styled.div`
  // position: fixed;
  width: 100%;
  box-sizing: border-box;
  margin: 0px;
  margin-top: 96px;
  font-size: 1.25rem;
  color: ${props => (props.balanceSOCKS ? props.theme.primary : 'white')};
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
`
const Account = styled.div`
  background-color: ${props => (props.balanceSOCKS ? '#f1f2f6' : props.theme.blue)};
  padding: 0.75rem;
  border-radius: 6px;
  cursor: ${props => (props.balanceSOCKS ? 'auto' : 'pointer')};
  display: flex;
  justify-content: center;
  align-items:center;
  transform: scale(1);
  transition: background-color 0.3s ease;

  :hover {
    background-color: ${props => (props.balanceSOCKS ? '#3333ff' : '#3333ff')};
    text-decoration: underline;
  }
`

const SockCount = styled.p`
  /* color: #6c7284; */
  font-weight: 500;
  margin: 0px;
  font-size: 14px;
  float: left;
`
const SpinnerWrapper = styled(Spinner)`
  margin: 0 0.25rem 0 0.3rem;


`

const Status = styled.div`
  display: ${props => (props.balanceSOCKS ? 'initial' : 'none')};
  width: 12px;
  height: 12px;
  border-radius: 100%;
  margin-left: 12px;
  margin-top: 2px;
  float: right;
  background-color: ${props =>
    props.account === null ? props.theme.orange :
      props.ready ? props.theme.green : props.theme.orange};
  // props.account === null ? props.theme.orange : props.theme.green};
`

export default function Body({
  ready, overlay, balanceETH, balanceLP, allowanceLP,
  rewardsPool, depositInfo,
  deposit, withdraw, validateDeposit, validateWithdraw,
  unlock, userCurrentReward, activeNetwork,
  apyVal
}) {
  const { account, library } = useWeb3Context()

  const [currentTransaction, _setCurrentTransaction] = useState({})

  const setCurrentTransaction = useCallback((hash, type, amount) => {
    // console.log('----- action working', currentTransaction)
    _setCurrentTransaction({ hash, type, amount })
  }, [])
  const clearCurrentTransaction = useCallback(() => {
    _setCurrentTransaction({})
  }, [])

  const pending = !!currentTransaction.hash
  useEffect(() => {
    if (currentTransaction.hash) {
      library.waitForTransaction(currentTransaction.hash).then(() => {
        clearCurrentTransaction()
      })
    }
  }, [currentTransaction, library, clearCurrentTransaction]);

  const [state] = useAppContext()
  const [showConnect, setShowConnect] = useState(false)
  const [selectedSubMenu, setSelectedSubMenu] = useState(0);

  if (!allowanceLP) allowanceLP = 0
  else allowanceLP = parseFloat(amountFormatter(allowanceLP));

  depositInfo = depositInfoFormatter(depositInfo);
  

 
  // let myTotalRewards = 0;
  // if (rewardsPool && depositInfo._totalDepositSeconds)
  //   myTotalRewards = amountFormatter(rewardsPool, TOKEN_ADDRESSES.SBSTOKEN_DEC) * depositInfo._userDepositSeconds / depositInfo._totalDepositSeconds;
  // console.log('----- depositInfo',depositInfo);
  return (
    <AppWrapper overlay={state.visible}>
      <Header
        ready={ready}
        setShowConnect={setShowConnect}
        allowanceLP={allowanceLP}
        unlock={unlock}
        currentTransactionType={currentTransaction.type}
        setCurrentTransaction={setCurrentTransaction}
        balanceETH={balanceETH}
        activeNetwork={activeNetwork}
        pending={pending}
      />
      <Content>
        <StatusShower>
          <StatusItem title="APY" value={`${apyVal}%`} styleLeft={0} description="APY is estimated for a new deposit over the next 30 days. The APY metric does not account for gains or losses from holding liquidity tokens, or gains from liquidity mining rewards distributed by the underlying platform for holding liquidity tokens." />
          <StatusItem title="Reward Muliplier" value={`${depositInfo._multiplierLevel}x`} description="Deposit liquidity tokens for 30 days to achieve a 3x reward multiplier." />
          <StatusItem title="Current Rewards" value={`${amountFormatter(userCurrentReward, TOKEN_ADDRESSES.SBSTOKEN_DEC)} SBS`} description="Your share of the total unlocked reward pool. Larger your deposit and for longer, higher your share." />
        </StatusShower>
        <SubMenuSelector>
          <SubMenuItemDiv onClick={() => { setSelectedSubMenu(0) }}
            style={selectedSubMenu === 0 ? { color: 'black', backgroundColor: 'white' } : {}} >Deposit</SubMenuItemDiv>
          <SubMenuItemDiv onClick={() => { setSelectedSubMenu(1) }}
            style={selectedSubMenu === 1 ? { color: 'black', backgroundColor: 'white' } : {}} >Withdraw</SubMenuItemDiv>
          <SubMenuItemDiv onClick={() => { setSelectedSubMenu(2) }}
            style={selectedSubMenu === 2 ? { color: 'black', backgroundColor: 'white' } : {}} >Stats</SubMenuItemDiv>
        </SubMenuSelector>
        <SubMenuContent>
          {(selectedSubMenu === 0) &&
            <DepositContent account={account}
              balanceLP={balanceLP} depositInfo={depositInfo} allowanceLP={allowanceLP}
              deposit={deposit} userCurrentReward={userCurrentReward}
              currentTransactionHash={currentTransaction.hash}
              currentTransactionType={currentTransaction.type}
              currentTransactionAmount={currentTransaction.amount}
              setCurrentTransaction={setCurrentTransaction}
              clearCurrentTransaction={clearCurrentTransaction}
              pending={pending}
            />}
          {(selectedSubMenu === 1) && <WithdrawContent account={account} balanceLP={balanceLP}
            depositInfo={depositInfo} withdraw={withdraw}
            currentTransactionHash={currentTransaction.hash}
            currentTransactionType={currentTransaction.type}
            currentTransactionAmount={currentTransaction.amount}
            setCurrentTransaction={setCurrentTransaction}
            clearCurrentTransaction={clearCurrentTransaction}
            pending={pending}
          />}
          {(selectedSubMenu === 2) && <StatsContent 
          account={account} depositInfo={depositInfo} 
          userCurrentReward={userCurrentReward} 
          apyVal={apyVal}
          />}
        </SubMenuContent>
      </Content>
      <Checkout
        ready={ready}
        unlock={unlock}
        deposit={deposit} withdraw={withdraw} validateDeposit={validateDeposit}
        validateWithdraw={validateWithdraw}
        setCurrentTransaction={setCurrentTransaction}
        clearCurrentTransaction={clearCurrentTransaction}
        currentTransactionHash={currentTransaction.hash}
        currentTransactionType={currentTransaction.type}
        currentTransactionAmount={currentTransaction.amount}
        setShowConnect={setShowConnect}
        showConnect={showConnect}
        activeNetwork={activeNetwork}
      />
    </AppWrapper>
  )
}

const AppWrapper = styled.div`
  width: 100%;
  height: 100%;
  margin: 0px auto;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  scroll-behavior: smooth;
  overflow: hidden;
  position: ${props => (props.overlay ? 'fixed' : 'initial')};
  box-sizing: border-box;
  z-index: 0;
`

const Content = styled.div`
  width: calc(100vw - 32px);
  max-width: 600px;
  min-width: 320px;
  // overflow: ${props => (props.overlay ? 'hidden' : 'auto')};
`

// const Unicorn = styled.p`
//   color: ${props => props.theme.uniswapPink};
//   font-weight: 600;
//   margin: auto 0px;
//   font-size: 16px;
// `

const StatusShower = styled.div`
  width: 100%;
  display: block;
  text-align: center;
  margin-bottom: 10px;
`

const SubMenuSelector = styled.div`
  width: 100%;
  display: flex;
  text-align: center;
  background-color: black;
  border: 1px solid black;
  border-radius: 8px;
  box-sizing: border-box;
`

const SubMenuItemDiv = styled.div`
  position: relative;
  display: inline-flex;
  flex: auto;
  margin: 0;
  height: auto;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0,0,0,.2);
  border-radius: 8px;
  padding: 5px 0;
  line-height: 1.5;
  box-sizing: border-box;
  overflow: unset;
  cursor: pointer;
  color: white;
  background-color: black;
  &:last-child{
    margin-right: 0;
  }
`

const SubMenuContent = styled.div`
  position: relative;
  display: flex;
  height: auto;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  flex-direction: column;
`
