import React, { useState } from 'react'

import styled from 'styled-components'
import { amountFormatter, TOKEN_ADDRESSES, TRADE_TYPES } from '../utils'
import Button from './Button'

const StatusItemDiv = styled.div`
  position: relative;
  display: inline-block;
  width: 100%;
  height: auto;
  padding: 10px 0;
  line-height: 1.5;
  box-sizing: border-box;
  overflow: unset;
  div
`

const BalanceDescription = styled.div`
  width: 100%;
  text-align: right;
  font-size: 12.5px;
`

const BalanceInput = styled.input`
  border: 1px solid rgba(0,0,0,.2);
  border-radius: 5px;
  padding: 16px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 600;
  outline: none;
`

const RewardStatus = styled.div`
  margin-top: 20px;
  position: relative;
  border-radius: 8px;
  border-left: 8px solid #ff2d55;
  padding: 16px 0;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  & > div{
    padding: 0 20px;
  }
  img{
    width: 50px;
    height: 50px;
  }
`


const StatusItemTitle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`

const StatusItemDescriptionBtn = styled.div`
  cursor: pointer;  
  font-size: 18px;
  line-height: 1;
  margin: 0 10px;
`

const StatusItemDescription = styled.div`
  position: absolute;
  right: 0;
  top: 50%;
  background-color: black;
  border-radius: 5px;
  color: white;
  padding: 10px;
  max-width: 300px;
  font-size: 14px;
`

const StatusItemValue = styled.div`
  text-align: left;
  width: 100%;
  font-weight: bold;
`

const WarningArea = styled.div`
  margin-top: 20px;
  position: relative;
  border-radius: 0;
  border-left: 8px solid #7424cc;
  background-color: #912dff;
  padding: 16px 20px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: white;
`

// const SubmitBtn = styled.button`
//   margin-top: 20px;
//   border: 1px solid rgba(0,0,0,.2);
//   border-radius: 5px;
//   padding: 16px;
//   width: 100%;
//   box-sizing: border-box;
//   font-size: 16px;
//   font-weight: 600;
//   outline: none;
//   cursor: pointer;
// `

export default function WithdrawContent({ account = false, value = "0.00",
  balanceLP, depositInfo,
  withdraw, pending,
  currentTransactionType, setCurrentTransaction
}) {
  const [showDesc, setShowDesc] = useState(false)
  const [isRquested, setIsRequested] = useState(false);
  const amountLP = amountFormatter(balanceLP, TOKEN_ADDRESSES.LPTOKEN_DEC, 8);

  const withdrawProcess = () => {
    setIsRequested(true);
    withdraw().then((response) => {
      setCurrentTransaction(
        response.hash,
        TRADE_TYPES.WITHDRAW,
        balanceLP
      )
      setIsRequested(false);
    });
  }
  // console.log('----depositInfo', depositInfo._depositTimestamp)
  let withdrawLocked = (!depositInfo._lpTokensDeposited || depositInfo._lpTokensDeposited === '0' || isRquested || pending);
  let lockedTimestamp = (depositInfo._depositTimestamp * 1000 + 24 * 3600 * 1000 - (+new Date()));
  if (lockedTimestamp > 0) {
    const hrs = parseInt(lockedTimestamp / (3600 * 1000));
    lockedTimestamp -= hrs * 3600 * 1000;
    const mins = parseInt(lockedTimestamp / (60 * 1000));
    lockedTimestamp -= mins * 60 * 1000;
    const secs = parseInt(lockedTimestamp / (1000));
    withdrawLocked = `Locked for ${hrs} hrs ${mins} mins`;
  } else if (withdrawLocked) withdrawLocked = `Locked`
  else withdrawLocked = '';

  return (
    <StatusItemDiv>
      {account && (
        <>
          <BalanceDescription >
            <p>Wallet Balance: {`${amountLP}`}&nbsp;<span >({TOKEN_ADDRESSES.LP})</span></p>
          </BalanceDescription>
          <BalanceInput type="text" placeholder="Enter Amount" min={0} step={0.01}
            value={`${depositInfo._lpTokensDeposited} ${TOKEN_ADDRESSES.LP}`} disabled />
          <RewardStatus>
            <div><img src={`${process.env.PUBLIC_URL}/img/logo.png`} alt="StaysBase" /></div>
            <div>
              <StatusItemTitle>
                <div>Your Total Deposited</div>
                <StatusItemDescriptionBtn
                  onClick={() => setShowDesc(true)}
                  onMouseOut={() => setShowDesc(false)}>
                  <svg viewBox="64 64 896 896" focusable="false" data-icon="info-circle" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                    <path d="M512 64C264.6 64 64 264.6 64 512s200.6 448 448 448 448-200.6 448-448S759.4 64 512 64zm0 820c-205.4 0-372-166.6-372-372s166.6-372 
                372-372 372 166.6 372 372-166.6 372-372 372z"></path>
                    <path d="M464 336a48 48 0 1096 0 48 48 0 10-96 0zm72 112h-48c-4.4 0-8 3.6-8 8v272c0 4.4 3.6 8 8 8h48c4.4 0 8-3.6 8-8V456c0-4.4-3.6-8-8-8z"></path>
                  </svg>
                </StatusItemDescriptionBtn>
              </StatusItemTitle>
              <StatusItemValue>{`${depositInfo._lpTokensDeposited}`} ({TOKEN_ADDRESSES.LP})</StatusItemValue>
              <StatusItemDescription style={{ display: (showDesc ? 'block' : 'none') }}>
                Total amount that you deposited. You can withdraw 24hrs after from deposited.
          </StatusItemDescription>
            </div>
          </RewardStatus>
        </>
      )}
      {!account && (
        <WarningArea>Connect your wallet to the Binance Smart Chain</WarningArea>
      )}
      {account && (
        <Button style={{ marginTop: '18px' }} disabled={(withdrawLocked) ? 'disabled' : ''}
          text={`Withdraw ${withdrawLocked}`}
          onClick={() => { withdrawProcess() }}
          pending={pending}
          currentTransactionType={currentTransactionType}
          tradeType={TOKEN_ADDRESSES.WITHDRAW}
        />
      )}
    </StatusItemDiv >
  )
}
