import React from 'react'
import styled from 'styled-components'

import { Spinner } from '../theme'
import Circle from './Gallery/circle.svg'

const ButtonFrame = styled.button`  
  border: 2px solid rgba(0,0,0,.2);
  border-radius: 10px;
  padding: 16px;
  width: 100%;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 600;
  outline: none;
  cursor: pointer;
  pointer-events: ${props => (props.disabled ? 'none' : 'auto')};
  background: white;
  // ${props => props.disabled ? '#f1f2f6' : 'linear-gradient(97.28deg, #fe6dde 2.08%, #ff9dea 106.51%)'};
  // box-shadow: ${props => (props.disabled ? 'none' : '0px 4px 20px rgba(239, 162, 250, 0.7)')};

  color: ${props => props.disabled ? props.theme.textDisabled : props.theme.black};
  transform: scale(1);
  // transition: border 0.3s ease;

  :hover {
    border: 2px solid #fe6dde;
  }

  :active :focus{
    border: 1px solid #fe6dde;
  }

  &:disabled{
    cursor: auto;
  }
`

const CtaText = styled.div`
  width: 100%;
`
const CtaIconImage = styled.img`
  width: 24px;
  height: 24px;
`

const CtaTextFlex = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`

const SpinnerWrapper = styled(Spinner)`
  margin: 0 0.25rem 0 0.3rem;
`

export default function Button({ onClick = () => { },
  preventDefault = true,
  text, icon,
  pending, currentTransactionType, tradeType, ...rest }) {
  return (
    <ButtonFrame
      onClick={e => {
        if (preventDefault) {
          e.preventDefault()
        }
        onClick(e)
      }}
      {...rest}
    >
      {(pending && currentTransactionType === tradeType) ? (
        <CtaTextFlex>
          {text}
          <SpinnerWrapper src={Circle} alt="loader" />{' '}
        </CtaTextFlex>
      ) : icon ? (
        <CtaTextFlex style={{ justifyContent: 'space-between' }}>
          {text}
          <CtaIconImage src={icon} alt="Icon" />{' '}
        </CtaTextFlex>
      ) : (
            <CtaText>{text}</CtaText>
          )}
    </ButtonFrame>
  )
}
