import React from 'react'

import styled from 'styled-components'
import { amountFormatter, dateTimeFormatter, TOKEN_ADDRESSES } from '../utils'

const StatusItemDiv = styled.div`
  position: relative;
  display: inline-block;
  width: 100%;
  height: auto;
  padding: 10px 0;
  line-height: 1.5;
  box-sizing: border-box;
  overflow: unset;
  div
`

const RewardStatus = styled.div`
  margin-top: 20px;
  position: relative;
  border-radius: 8px;
  border-left: 8px solid #ff2d55;
  padding: 16px 0;
  width: 100%;
  box-sizing: border-box;
  display: block;
  text-align: center;
  white-space: wrap-all;
  align-items: center;  

  & > div{
    margin: 15px 0;
    padding: 0 20px;
    display: inline-block;
    width: 48%;
    text-align: left;
    box-sizing: border-box;
  }
`

const StatusItemTitle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`
const StatusItemValue = styled.div`
  text-align: left;
  width: 100%;
  font-weight: bold;
`

export default function StatsContent({ account = false, value = "400", apyVal, depositInfo, userCurrentReward }) {
  // const [showDesc, setShowDesc] = useState(false)
  var timeStaked = '- -';
  if (depositInfo._depositTimestamp) timeStaked = dateTimeFormatter(depositInfo._depositTimestamp * 1000);
  var pcntOfPool = '0.00';
  if (depositInfo._totalDepositSeconds && depositInfo._userDepositSeconds)
    pcntOfPool = (depositInfo._userDepositSeconds / depositInfo._totalDepositSeconds * 100).toFixed(2);
  return (
    <StatusItemDiv>
      <RewardStatus>
        <div>
          <StatusItemTitle>
            <div>Deposited Balance</div>
          </StatusItemTitle>
          <StatusItemValue>{`${depositInfo._lpTokensDeposited}`} {TOKEN_ADDRESSES.LP}</StatusItemValue>
        </div>
        <div>
          <StatusItemTitle>
            <div>Estimated Total Rewards</div>
          </StatusItemTitle>
          <StatusItemValue>{`${amountFormatter(userCurrentReward, TOKEN_ADDRESSES.SBSTOKEN_DEC)}`} {TOKEN_ADDRESSES.SBS}</StatusItemValue>
        </div>
        <div>
          <StatusItemTitle>
            <div>APY</div>
          </StatusItemTitle>
          <StatusItemValue>{apyVal}%</StatusItemValue>
        </div>
        <div>
          <StatusItemTitle>
            <div>% of Pool</div>
          </StatusItemTitle>
          <StatusItemValue>{`${pcntOfPool}`} %</StatusItemValue>
        </div>
        <div>
          <StatusItemTitle>
            <div>Time Staked</div>
          </StatusItemTitle>
          <StatusItemValue>{timeStaked}</StatusItemValue>
        </div>
        <div>
          <StatusItemTitle>
            <div>Reward Multiplier</div>
          </StatusItemTitle>
          <StatusItemValue>{depositInfo._multiplierLevel}x</StatusItemValue>
        </div>
      </RewardStatus>
    </StatusItemDiv >
  )
}
